.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 10px 10px;
  overflow: hidden;
}

.p-browser {
  height: 30px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-title {
  width: 100%;
  text-align: center;
  color: lightgray;
}

.p-img {
  width: 60%;
  height: 60%;
  margin-top: 30px;
  object-fit: scale-down;
}

.p-desc {
  margin-top: 10px;
  display: block;
}
